.source {
    border: 1px solid black;
    width: 15%;
    margin: 10px 10px;
    background-color: aquamarine;
    border-radius: 10px;
    display: inline-block;
}

img {
    width: 150px;
    height: 200px;
    margin: 10px 20px;
    border-radius: 15px;
}

.main {
    margin: 10px 10px;
}

.But {
    height: 40px;
    width: 30%;
    border-radius: 5px;
    background-color: brown;
    color: azure;
    margin-right: 10px;
    line-height: 1px;
}

.but {
    text-decoration: none;
    display: flex;
    justify-content: center;
    font-size: 15px;
    color: beige;

}

.Flex {
    display: flex;
    justify-content: space-between;

}